import React from 'react';
import { Row , Col } from 'react-flexbox-grid';
import Container, { Slides, Slide, Inner } from './styled';
import Button, { ButtonGroup } from './../Button';

export default () => (
  <Container id="slides" >
    <h2>   <span></span></h2>
    <p>
          .   <br/>     ,  .
    </p>
    <Slides>
      <Slide backward >
        <h6>    </h6>
      </Slide>
      <Slide center >
        <h5>    </h5>
      </Slide>
      <Slide >
        <Inner>
          <h5>     I </h5>
          <Row>
            <Col xs={12} lg={7}>
              <small>
                    31  2018    8096,25,       I .
              </small>
            </Col>
            <Col xs={12} lg={5} style ={{ display: 'flex', justifyContent: 'flex-end'}} >
              <ButtonGroup>
                <Button style={{padding: '13px 38px'}} inactive disabled outline type="button"></Button>
                <Button inactive disabled outline small type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  &#8226; &#8226; &#8226;
                </Button>
              </ButtonGroup>
            </Col>
          </Row>
        </Inner>
        <Button disabled footer type="button"></Button>
      </Slide>
    </Slides>
  </Container>
);



